import dayjs from "dayjs";
import _ from "lodash";
import React, { useCallback, useState } from "react";
import { useForm } from "react-hook-form";
import styled from "styled-components";

import { Button } from "@components/Button/styles";
import ErrorValidate from "@components/ErrorValidate";
import { Input } from "@components/Input/styles";
import { Text } from "@components/Text/styles";
import { useSocketContext } from "@contexts/socket/SocketContext.context";
import { COLOR_RED_1, COLOR_WHITE } from "@styles/color.styles";
import { useSocketMessage } from "@utils/useSocketMessage.utils";
import { validate } from "@validate/index";

const ExampleSocketContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const ExampleSocketSectionRegister = styled.div`
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
`;

const ExampleSocketSectionChatMessage = styled.div`
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
`;

export const ExampleSocket = () => {
  const [user, setUser] = useState(null);
  const [message, setMessage] = useState([]);
  const socket = useSocketContext();

  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm();

  const onSubmitRegister = (input) => {
    const mockUpUserId = dayjs().unix();
    setUser({ id: mockUpUserId, username: _.get(input, ["username"]) });
  };

  const onSubmitSendMessage = (input) => {
    setMessage([
      ...message,
      {
        user: user,
        message: _.get(input, ["message"]),
      },
    ]);
    socket.emit("client-send-chat-message", {
      user: user,
      message: _.get(input, ["message"]),
    });
    reset();
  };

  const onClientReceivedMessage = useCallback(
    ({ user: receivedUser, message: receivedMessage }) => {
      if (_.get(user, ["id"]) !== _.get(receivedUser, ["id"])) {
        setMessage((prevMessage) => [
          ...prevMessage,
          {
            user: receivedUser,
            message: receivedMessage,
          },
        ]);
      }
    },
    [user]
  );

  useSocketMessage({
    event: "client-received-chat-message",
    callback: onClientReceivedMessage,
  });

  return (
    <ExampleSocketContainer>
      <div>ExampleSocket</div>
      <div>Chat Message</div>
      {!user ? (
        <>
          <div>Register</div>

          <ExampleSocketSectionRegister>
            <form onSubmit={handleSubmit(onSubmitRegister)}>
              <Input
                {...register("username", _.get(validate, ["required"]))}
                type="text"
                placeholder="TEXT"
              />
              <ErrorValidate errors={errors} name={`username`} />

              <Button
                color={COLOR_RED_1}
                fontColor={COLOR_WHITE}
                fontWeight="bold"
                padding="8px 28px"
              >
                Register
              </Button>
            </form>
          </ExampleSocketSectionRegister>
        </>
      ) : (
        <>
          <div>Username : {_.get(user, ["username"])}</div>
          <ExampleSocketSectionChatMessage>
            {_.map(message, (item, index) => {
              return <Text key={index}>{_.get(item, ["message"])}</Text>;
            })}
          </ExampleSocketSectionChatMessage>
          <ExampleSocketSectionRegister>
            <form onSubmit={handleSubmit(onSubmitSendMessage)}>
              <Input
                {...register("message", _.get(validate, ["required"]))}
                type="text"
                placeholder="TEXT"
              />
              <ErrorValidate errors={errors} name={`message`} />

              <Button
                color={COLOR_RED_1}
                fontColor={COLOR_WHITE}
                fontWeight="bold"
                padding="8px 28px"
              >
                Send
              </Button>
            </form>
          </ExampleSocketSectionRegister>
        </>
      )}
    </ExampleSocketContainer>
  );
};
