import styled, { css } from "styled-components";

import { COLOR_BLACK } from "@styles/color.styles";
import { FONT_SIZE_S } from "@styles/size.styles";

// ===== [props] =====
// - color => default: COLOR_BLACK
// - fontWeight => default: 400
// - textDecorationLine => default: none
// - opacity => default: 1
// - lineHeight => default: normal
// - align => default: start
// - fontSize => default: HOC
// - isEllipsis && line => default: 3 lines

export const Text = styled.p`
  color: ${({ color = COLOR_BLACK }) => color};
  font-weight: ${({ fontWeight = "400" }) => fontWeight};
  text-decoration-line: ${({ textDecorationLine = "none" }) =>
    textDecorationLine};
  opacity: ${({ opacity = 1 }) => opacity};
  line-height: ${({ lineHeight = "normal" }) => lineHeight};
  text-align: ${({ align = "start" }) => align};
  white-space: pre-wrap;
  ${({ fontSize }) =>
    fontSize &&
    css`
      font-size: ${({ fontSize = FONT_SIZE_S }) => `${fontSize}`};
    `}

  ${({ isEllipsis }) =>
    isEllipsis &&
    css`
      text-overflow: ellipsis;
      overflow: hidden;
      display: -webkit-box;
      -webkit-line-clamp: ${({ lines = 3 }) => lines};
      -webkit-box-orient: vertical;
      overflow: hidden;
    `}
`;
