import _ from "lodash";
import React, { useState } from "react";
import styled from "styled-components";

import { Text } from "@components/Text/styles";
import { useInnerHeight } from "@utils/useInnerHeight.utils";

import { ExampleComponents } from "./subComponents/ExampleComponents";
import { ExampleHookform } from "./subComponents/ExampleHookform";
import { ExampleRedux } from "./subComponents/ExampleRedux";
import { ExampleSocket } from "./subComponents/ExampleSocket";
import { ExampleUtils } from "./subComponents/ExampleUtils";

const ExampleContainer = styled.div`
  width: 100%;
  height: ${({ height }) => height};
  display: flex;
  /* justify-content: center; */
  flex-direction: column;
`;

const ExampleNavbar = styled.div`
  width: 100%;
  height: auto;
  display: flex;
  gap: 10px;
  justify-content: center;
`;

const ExampleSectionComponent = styled.div``;

const NAV_LIST = [
  { to: "hook_form", label: "hook form" },
  { to: "socket", label: "socket" },
  { to: "redux", label: "redux" },
  { to: "utils", label: "utils" },
  { to: "components", label: "components" },
];

export default () => {
  const [showComponent, setShowComponent] = useState(
    _.chain(NAV_LIST).first().get(["to"]).value()
  );
  const onHandleChangeComponents = ({ to }) => {
    setShowComponent(to);
  };

  return (
    <ExampleContainer height={useInnerHeight()}>
      <ExampleNavbar>
        {_.map(NAV_LIST, (item, index) => {
          return (
            <Text
              key={index}
              textDecorationLine="underline"
              onClick={() =>
                onHandleChangeComponents({ to: _.get(item, ["to"]) })
              }
            >
              {_.get(item, ["label"])}
            </Text>
          );
        })}
      </ExampleNavbar>
      <ExampleSectionComponent>
        {(() => {
          switch (showComponent) {
            case "hook_form":
              return <ExampleHookform />;
            case "socket":
              return <ExampleSocket />;
            case "components":
              return <ExampleComponents />;
            case "redux":
              return <ExampleRedux />;
            case "utils":
              return <ExampleUtils />;
            default:
              return <ExampleHookform />;
          }
        })()}
      </ExampleSectionComponent>
    </ExampleContainer>
  );
};
