import _ from "lodash";
import React from "react";
import { useDispatch, useSelector } from "react-redux";
import styled from "styled-components";

import { Button } from "@components/Button/styles";
import { setCount } from "@redux/reducers/guide.reducers";
import { COLOR_RED_1, COLOR_WHITE } from "@styles/color.styles";

const ExampleReduxContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const ExampleReduxSectionForm = styled.div`
  width: 50%;
  display: flex;
  justify-content: center;
  gap: 10px;
`;

const ExampleReduxSectionAction = styled.div`
  display: flex;
  align-self: center;
`;

export const ExampleRedux = () => {
  const dispatch = useDispatch();
  const { count } = useSelector(({ guide }) => {
    return {
      count: _.get(guide, ["count"]),
    };
  });
  return (
    <ExampleReduxContainer>
      <div>ExampleRedux</div>
      <div>REDUX : {count}</div>
      <ExampleReduxSectionForm>
        <Button
          color={COLOR_RED_1}
          fontColor={COLOR_WHITE}
          fontWeight="bold"
          padding="8px 28px"
          onClick={() => dispatch(setCount(count + 1))}
        >
          +
        </Button>
        <Button
          color={COLOR_RED_1}
          fontColor={COLOR_WHITE}
          fontWeight="bold"
          padding="8px 28px"
          onClick={() => dispatch(setCount(count - 1))}
        >
          -
        </Button>
      </ExampleReduxSectionForm>
    </ExampleReduxContainer>
  );
};
