import { ErrorMessage } from "@hookform/error-message";
import React from "react";
import styled from "styled-components";

import { COLOR_RED_1 } from "@styles/color.styles";
import { FONT_SIZE_XS } from "@styles/size.styles";

const ErrorValidateMessage = styled.span`
  color: ${COLOR_RED_1};
  font-weight: bold;
  font-size: ${FONT_SIZE_XS};
`;

export default ({ errors, name }) => {
  return (
    <ErrorMessage
      errors={errors}
      name={name}
      render={({ message }) => (
        <ErrorValidateMessage>{message}</ErrorValidateMessage>
      )}
    />
  );
};
