import _ from "lodash";
import React from "react";
import { useForm } from "react-hook-form";
import styled from "styled-components";

import { Button } from "@components/Button/styles";
import ErrorValidate from "@components/ErrorValidate";
import { Input } from "@components/Input/styles";
import { COLOR_RED_1, COLOR_WHITE } from "@styles/color.styles";
import { validate } from "@validate";
const ExampleHookFormContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const ExampleHookFormSectionForm = styled.div`
  width: 50%;
  display: flex;
  flex-direction: column;
  gap: 10px;
`;

const ExampleHookFormSectionAction = styled.div`
  display: flex;
  align-self: center;
`;
export const ExampleHookform = () => {
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();

  const onSubmit = (input) => {
    alert(JSON.stringify(input));
  };
  return (
    <ExampleHookFormContainer>
      <div>ExampleHookform</div>
      <ExampleHookFormSectionForm>
        <form onSubmit={handleSubmit(onSubmit)}>
          <Input
            {...register("number", _.get(validate, ["number"]))}
            type="text"
            placeholder="NUMBER"
          />
          <ErrorValidate errors={errors} name={`number`} />
          <Input
            {...register("text-require", _.get(validate, ["required"]))}
            type="text"
            placeholder="TEXT"
          />
          <ErrorValidate errors={errors} name={`text-require`} />
          <ExampleHookFormSectionAction>
            <Button
              color={COLOR_RED_1}
              fontColor={COLOR_WHITE}
              fontWeight="bold"
              padding="8px 28px"
            >
              Submit
            </Button>
          </ExampleHookFormSectionAction>
        </form>
      </ExampleHookFormSectionForm>
    </ExampleHookFormContainer>
  );
};
