import styled, { css } from "styled-components";

import { COLOR_GAY_2, COLOR_GAY_5, COLOR_WHITE } from "@styles/color.styles";
import { BORDER_BUTTON_SIZE, FONT_SIZE_XS } from "@styles/size.styles";

export const Button = styled.button`
  display: flex;
  justify-content: center;
  align-items: center;
  margin: ${({ margin = "0px" }) => margin};
  background-color: ${({ color = "transparent" }) => `${color}`};
  font-weight: ${({ fontWeight = 500 }) => `${fontWeight}`};
  ${({ fontSize }) =>
    fontSize &&
    css`
      font-size: ${({ fontSize = FONT_SIZE_XS }) => `${fontSize}`};
    `}

  ${({ fontColor }) =>
    fontColor &&
    css`
      color: ${({ fontColor = COLOR_WHITE }) => `${fontColor}`};
    `}

  ${({ fontWeight }) =>
    fontWeight &&
    css`
      color: ${({ fontWeight = "400" }) => `${fontWeight}`};
    `}
    
  ${({ padding }) =>
    padding &&
    css`
      padding: ${({ padding = "5px 10px" }) => padding};
    `}
  width: fit-content;
  border-radius: ${BORDER_BUTTON_SIZE};
  &:disabled {
    background-color: ${COLOR_GAY_2};
    color: ${COLOR_GAY_5};
    cursor: not-allowed;
  }
`;
